/**
 * EXTEND PATTERN
 */
const stringNumber = /^[0-9]*$/
const normalNumber = /^\d+(\.\d+)?$/
const account = /^[a-zA-Z0-9_-]{4,}$/
const cellPhone = /^1[3456789]\d{9}$/
const complexPassword = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/
const simplePassword = /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?!([^(0-9a-zA-Z)]|[\(\)])+$)([^(0-9a-zA-Z)]|[\(\)]|[a-z]|[A-Z]|[0-9]){6,}$/
const qq = /^[a-zA-Z0-9_-]{4,16}$/
const dictTypeCode = /^[0-9a-zA-Z_]{1,10}$/
const dictOther = /^[\s\S]{1,30}$/
const dictNum = /^[0-9]{1,3}$/
const paramsOther = /^[\s\S]{1,50}$/
const vehicleNumber = /^[京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领A-Z]{1}[A-Z]{1}[A-Z0-9]{4,5}[A-Z0-9挂学警港澳]{1}$/
const telPhone = /^((0\d{2,3})-?)(\d{7,8})(-(\d{3,}))?$/
const positiveInteger = /^[1-9]{1,}[\d]*$/
const price = /^(0|([1-9][0-9]*))(\.\d{1,2})?$/
const email =  /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/
const percent = /^((\d{1,2}(\.\d{1,2})?)|100)$/
const bigPrice = /^([1-9][0-9]*)(\.\d{1,2})?$/
const postCode = /^\d{6}$/
// const IdNo = /^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/
const IdNo = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
const orgCode = /^[a-zA-Z0-9]{8}-[a-zA-Z0-9]$/
const bankCard = /^([1-9]{1})(\d{14}|\d{15}|\d{18})$/
const zhengzhengshu =  /^(?:(?!0)\d{1,6}|10{6})$/

export default {
  stringNumber,
  normalNumber,
  account,
  cellPhone,
  complexPassword,
  simplePassword,
  qq,
  dictTypeCode,
  dictOther,
  dictNum,
  paramsOther,
  vehicleNumber,
  telPhone,
  positiveInteger,
  price,
  email,
  percent,
  bigPrice,
  postCode,
  IdNo,
  orgCode,
  bankCard,
  zhengzhengshu
}



