import http from '@/utils/http'
/**
 * USER API
 */

/**
 * 获取用户列表
 * @param {*} params
 */
export function getUsersList (params) {
  return http.post(
    '/user/userpage/get',
    params
  )
}

/**
 * 用户详情
 * @param {*} params
 */
export function getUserDetail (userId) {
  return http.post(
    '/user/userdetail/get',
    userId
  )
}

/**
 * 新建用户
 * @param {*} params
 */
export function createUser (params) {
  return http.post(
    '/user/createUser',
    params
  )
}

/**
 * 修改用户
 * @param {*} params
 */
export function updateUser (params) {
  return http.upload(
    '/user/editUser',
    params
  )
}

/**
 * 删除用户
 * @param {*} params
 */
export function deleteUser (userIds) {
  return http.post(
    '/user/deleteUser',
    userIds
  )
}

/**
 * 重置密码
 * @param {*} params
 */
export function resetPassword (userId) {
  return http.post(
    '/user/resetpassword/update',
    userId
  )
}

/**
 * 设置用户角色
 * @param {*} params
 */
export function setUserRole (params) {
  return http.post(
    '/userRole/rolegrant',
    params
  )
}



