/**
 * Validate normal string number
 * @param {*} rule
 * @param {*} value
 * @param {*} callback
 */
const strNumber = (rule, value, callback) => {
  if (value) {
    const errors = []
    if (!regPattern.stringNumber.test(Number(value))) {
      errors.push(new Error('只能输入数字'))
      callback(errors)
    } else {
      callback()
    }
  } else {
    callback()
  }
}

